@import './_mixins/_font-face';

@include font-face('Museo', '../fonts/Museo/light', $weight: 300);
@include font-face('Museo', '../fonts/Museo/Regular');
@include font-face('Museo', '../fonts/Museo/Medium', $weight: 600);
@include font-face('Museo', '../fonts/Museo/Bold', $weight: 800);
@include font-face('Museo', '../fonts/Museo/Black', $weight: 900);

